import { useEffect, useState } from 'react';
import './Home.css';
import PostHeader from './PostHeader';
import Loader from './Loader';
import ReactMarkdown from 'react-markdown';

const LOADING_STATE = { loading: 0, success: 1, error: 2 };

function Home() {
  const user = JSON.parse(localStorage.getItem('user'));
  const [loadingState, setLoadingState] = useState(LOADING_STATE.loading);
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch('/api/blog/', {
      method: 'GET',
      headers: {
        'auth-token': user?.['auth-token'],
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then((r) => r.json())
      .then((d) => {
        setData(d);
        setLoadingState(LOADING_STATE.success);
      })
      .catch((e) => {
        console.error(e);
        setLoadingState(LOADING_STATE.error);
      });
  }, []);

  if (loadingState === LOADING_STATE.loading) return <Loader />;
  if (loadingState === LOADING_STATE.error)
    return (
      <div className="error">
        <h3>Error: could not load</h3>
      </div>
    );

  return (
    <div className="column home">
      {data.map((item, key) => {
        // Create a text-only preview by stripping Markdown
        const rawText = item.content
          ?.replace(/!\[.*?\]\(.*?\)/g, '') // remove images
          .replace(/\[.*?\]\(.*?\)/g, '') // remove links
          .replace(/[#>*_`-]/g, '') // remove markdown symbols
          .slice(0, 200);

        return (
          <div className={`postHome${item.public ? '' : ' private'}`} key={key}>
            <a href={`/blog/post/${item.id}`}>
              <PostHeader post={item} />
              <p>{rawText}...</p>
            </a>
          </div>
        );
      })}
    </div>
  );
}

export default Home;
