import './New.css';
import React from 'react';
import { Navigate } from 'react-router-dom';
import { withRouter } from './HOC';
import Loader from './Loader';
import MdEditor from 'react-markdown-editor-lite';
import Markdown from 'react-markdown';
import 'react-markdown-editor-lite/lib/index.css';

const LOADING_STATE = { loading: 0, success: 1, error: 2, redirect: 3 };

class Edit extends React.Component {
  constructor(props) {
    super(props);
    this.id = this.props.params.id;
    this.state = {
      title: '',
      public: false,
      content: '',
      loadingState: LOADING_STATE.loading,
    };

    this.user = JSON.parse(localStorage.getItem('user'));
    this.save = this.save.bind(this);
    this.titleChanged = this.titleChanged.bind(this);
    this.publicChanged = this.publicChanged.bind(this);
    this.handleEditorChange = this.handleEditorChange.bind(this);
  }

  componentDidMount() {
    fetch(`/api/blog/post/${this.id}`, {
      method: 'GET',
      headers: {
        'auth-token': this.user?.['auth-token'],
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          title: data.title,
          public: data.public,
          content: data.content || '', // fallback if missing
          loadingState: LOADING_STATE.success,
        });
      })
      .catch((e) => {
        console.error(e);
        this.setState({ loadingState: LOADING_STATE.error });
      });
  }

  handleEditorChange({ text }) {
    this.setState({ content: text });
  }

  async save() {
    const data = {
      title: this.state.title,
      content: this.state.content,
      project: 'none',
      author: this.user._id,
      public: this.state.public,
    };

    fetch(`/api/blog/post/${this.id}/edit`, {
      method: 'POST',
      headers: {
        'auth-token': this.user['auth-token'],
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
      .then(() => {
        this.setState({ loadingState: LOADING_STATE.redirect });
      })
      .catch((e) => {
        console.error(e);
        alert('Did not save: some error happened');
      });
  }

  titleChanged(e) {
    this.setState({ title: e.target.value });
  }

  publicChanged(e) {
    this.setState({ public: e.target.checked });
  }

  render() {
    switch (this.state.loadingState) {
      case LOADING_STATE.redirect:
        return <Navigate to={`/post/${this.id}`} />;
      case LOADING_STATE.loading:
        return <Loader />;
      case LOADING_STATE.error:
        return (
          <div className="error">
            <h3>Error: Could not load</h3>
          </div>
        );
      case LOADING_STATE.success:
      default:
        return (
          <article>
            {!this.user && <Navigate to="/blog" />}
            <fieldset>
              <label htmlFor="titleInput">Title</label>
              <input
                type="text"
                name="title"
                id="titleInput"
                value={this.state.title}
                onChange={this.titleChanged}
              />
            </fieldset>
            <fieldset>
              <label htmlFor="publicInput">Public</label>
              <input
                type="checkbox"
                name="public"
                id="publicInput"
                checked={this.state.public}
                onChange={this.publicChanged}
              />
            </fieldset>

            <MdEditor
              value={this.state.content}
              style={{ height: '500px' }}
              renderHTML={(text) => <Markdown>{text}</Markdown>}
              onChange={this.handleEditorChange}
            />

            <button onClick={this.save}>SAVE</button>
          </article>
        );
    }
  }
}

export default withRouter(Edit);
