import './New.css';
import React from 'react';
import { Navigate } from 'react-router-dom';
import MdEditor from 'react-markdown-editor-lite';
import Markdown from 'react-markdown';
import 'react-markdown-editor-lite/lib/index.css';

const LOADING_STATE = { loading: 0, success: 1, error: 2, redirect: 3 };

class New extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      loadingState: LOADING_STATE.success,
      title: '',
      public: false,
      content: '',
    };

    this.user = JSON.parse(localStorage.getItem('user'));
    this.save = this.save.bind(this);
    this.titleChanged = this.titleChanged.bind(this);
    this.publicChanged = this.publicChanged.bind(this);
    this.handleEditorChange = this.handleEditorChange.bind(this);
  }

  handleEditorChange({ text }) {
    this.setState({ content: text });
  }

  async save() {
    const data = {
      title: this.state.title,
      content: this.state.content,
      project: 'none',
      author: this.user._id,
      public: this.state.public,
    };

    try {
      const res = await fetch('/api/blog/new', {
        method: 'POST',
        headers: {
          'auth-token': this.user['auth-token'],
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
      const result = await res.json();
      this.setState({
        id: result.id,
        loadingState: LOADING_STATE.redirect,
      });
    } catch (e) {
      alert("Didn't save: some error happened");
    }
  }

  titleChanged(e) {
    this.setState({ title: e.target.value });
  }

  publicChanged(e) {
    this.setState({ public: e.target.checked });
  }

  render() {
    if (!this.user) return <Navigate to="/blog" />;
    if (this.state.loadingState === LOADING_STATE.redirect) {
      return <Navigate to={'/post/' + this.state.id} />;
    }

    return (
      <article>
        <fieldset>
          <label htmlFor="titleInput">Title</label>
          <input
            type="text"
            name="title"
            id="titleInput"
            value={this.state.title}
            onChange={this.titleChanged}
          />
        </fieldset>
        <fieldset>
          <label htmlFor="publicInput">Public</label>
          <input
            type="checkbox"
            name="public"
            id="publicInput"
            checked={this.state.public}
            onChange={this.publicChanged}
          />
        </fieldset>

        <MdEditor
          value={this.state.content}
          style={{ height: '500px' }}
          renderHTML={(text) => <Markdown>{text}</Markdown>}
          onChange={this.handleEditorChange}
        />

        <button onClick={this.save}>SAVE</button>
      </article>
    );
  }
}

export default New;
