import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import hljs from 'highlight.js';
import 'highlight.js/styles/github.css'; // Or any other theme
import PostHeader from './PostHeader';
import Loader from './Loader';
import './Post.css';

const LOADING_STATE = { loading: 0, success: 1, error: 2 };

function Post() {
  const user = JSON.parse(localStorage.getItem('user'));
  const { id } = useParams();
  const [loadingState, setLoadingState] = useState(LOADING_STATE.loading);
  const [data, setData] = useState({
    author: {
      name: 'aelmardhi',
      profileImage:
        'https://res.cloudinary.com/dzrwq9pyu/image/upload/v1589248125/profile_images/5eb58733e838f7001761adfb.jpg',
    },
    project: 'none',
    time: 1656228357017,
    content: '## Sample Markdown\nSome content here.',
    title: 'New Blog',
  });

  useEffect(() => {
    fetch(`/api/blog/post/${id}`, {
      method: 'GET',
      headers: {
        'auth-token': user?.['auth-token'],
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then((r) => r.json())
      .then((d) => {
        setData(d);
        setLoadingState(LOADING_STATE.success);
      })
      .catch(() => {
        setLoadingState(LOADING_STATE.error);
      });
  }, []);

  useEffect(() => {
    // Apply syntax highlighting after content updates
    hljs.highlightAll();
  }, [data.content]);

  if (loadingState === LOADING_STATE.loading) return <Loader />;
  if (loadingState === LOADING_STATE.error)
    return (
      <div className="error">
        <h3>Error: could not load</h3>
      </div>
    );

  return (
    <article className="post">
      <div className="row spaceBetween alignCenter">
        <PostHeader post={data} />
        {data.author._id === user?._id && (
          <Link className="actionBtn" to={`/post/${id}/edit`}>
            Edit
          </Link>
        )}
      </div>
      <div className="postContent markdown-body">
        <ReactMarkdown>{data.content}</ReactMarkdown>
      </div>
    </article>
  );
}

export default Post;
